body{

  margin: 0;
  padding: 0;
}


.signup_link {
  text-align: center;
  font-size: 16px;
  color: white;
}

.signup_link a:hover {
  text-decoration: underline;
}
#loginB {
  color: white !important;
}

.p-5 {
  background-color: var(--color2) !important;
}

.group {
  margin-bottom: 20px !important;
}

.a {
  align-items: center !important;
  justify-content: center;

  border-radius: 10px;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
  background-color: white !important;
}

#loginForm {
  height: 350px;
  width: 330px;
  padding-top: 50px !important;
  margin-bottom: 0 !important;
  margin-left: auto;
  margin-right: auto;
}

#titulo {
  border-bottom: 1px solid silver !important;
  color: cornflowerblue!important;
}

.home{
    flex: 4 1;
}



.sidebar {
  flex: 1 1;
  height: calc(100vh - 50px);
  background-color: rgb(251, 251, 255);
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
}

.sidebarWrapper {
  padding: 20px;
  color: #555;
}

.sidebarMenu {
  margin-bottom: 10px;
}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(240, 240, 255);
}

.sidebarIcon {
  margin-right: 5px;
  font-size: 20px !important;
}
i.icon-inicio::before{

  color: #555;;
}
nav.navbar.navbar-expand.navbar-light {
  width: 100% !important;
  height: 50px !important;
  background-color: white !important;
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0 !important;
  z-index: 999 !important;
}

a.navbar-brand {
  margin: 0px !important;
  padding: 0;
}

.hola {
  margin: 0;
  padding: 0 !important;
}

b {
  color:cornflowerblue;
}

table.table.table-sm.table-striped.table-bordered.table-hover{
    width: 82%;
    margin-bottom: 26rem;
}

i.icon-editar::before{

    color:black;
}
.form-group {
  margin-bottom: 12px;
}

.alert {
  color: var(--color4);

  position: absolute !important;
  font-size: 14px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

  padding: 0 !important;
}

#factura {
  display: none;
}


@media print {
   #noPrint {
    display: none;
  } 

  nav.navbar.navbar.navbar-expand.navbar-light {
    display: none;
  }

  div.sidebar {
    display: none;
  }

